import { useContext } from "react"
import { Link } from "react-router-dom"
import Context from "../../Context/Context"
import { useDispatch } from "react-redux"
import './treads.css'
function Treads(){
    let dispatch = useDispatch()
    const {setIsContacts} = useContext(Context)
    return(
        <div className="all_treads c_hover">
            <i className="far chat c_hover fa-comment"></i>
            <Link to='/treadsBl' style={{textDecoration: 'none', color: '#fff'}} onClick={()=> dispatch({type:'REMOVE_CT'})} className="c_hover">Messenger</Link>
        </div>
    )
}
export default Treads