import { useContext } from "react"
import Context from "../../Context/Context"
import { Link, useNavigate } from "react-router-dom";
import './tread_bl.css'

function TreadBl(prop) {
    const {currentUser, usersDate} = useContext(Context)

    let current;
    usersDate.forEach(item=>{
        if(item.id == prop.user){
            current = item
        } 
    })
    let lastMess
    
    console.log(prop.user);
        
    if(currentUser.treads[prop.user]){
        lastMess = currentUser.treads[prop.user][currentUser.treads[prop.user].length-1]
        console.log(lastMess);
  return (
      
        <Link to={'/treads'} onClick={()=> prop.ChoseTread(prop.user)} id={prop.user} className="tread_item" >
            <div ><img src={require(`../../${current.img}`)} className="tread_item_img"  alt=""/> </div>
            <div className="tread_item_main">
            <div className="tread_item_name">{current.name}</div>
            <div className="last_message">{lastMess.postContent}</div>
            </div>
        </Link>
        
  )
    }
}
export default TreadBl