import { useContext } from "react"
import Context from "../../Context/Context"
import Post from "./Post";
import scrolling from "../../hooks/scrolling";
import { useEffect } from "react";
import './news.css';
import { useState } from "react";


function News(props){
    const {postsDate, setPostsDate, isPosts, currentUser} = useContext(Context)
    let [deletedPosts, setDeletedPosts] = useState([])

    useEffect(()=>{
        scrolling(props.news, window.screen.width) 
    }, [postsDate.length])

    const posts = postsDate?.map(item=> (item.map(item=><Post key={Math.random()} pd={postsDate} cu={currentUser} dp={deletedPosts} sdp={setDeletedPosts} item={item}/>)))


    if(isPosts && postsDate[0]){
    return(
        <div className="news_main">
            {posts}
        </div>
    )
    }
    else{
        return(
            <div className="news_main">
                <h1>Loading...</h1>
                </div>)
    }
    
        
    
}
export default News