import BioContacts from "../Contacts/BioContacts"
import Button from "../Button/Button"
import Header from "../Header/Header"
import Img from "../Img/Img"
import React, { useContext, useEffect, useRef} from "react"
import Context from "../../Context/Context"
import scrolling from "../../hooks/scrolling"
import ExBut from "../ExBut/ExBut"
import { useDispatch } from "react-redux"
import './bio.css'
import { Link } from "react-router-dom"

function MobileBio() {
    let dispatch = useDispatch()
    const {currentUser, currentPerson, mobileBio} = useContext(Context)
    let item;
    currentPerson.id ? item = currentPerson: item = currentUser
    let mobile_bio = useRef()
    let close_but = useRef()
    useEffect(()=>{
        dispatch({type: 'SET_MB'})
        dispatch({type: 'REMOVE_CT'})
    }, [currentPerson]);

    function closeBio(){
        dispatch({type: 'REMOVE_MB'})
    }

   
    

    return(
        <div ref={mobile_bio} className="mobile_bio" style={{display: mobileBio, top: 0}}>
            <div className="container_cont_m">
            <Link to={'/general'}><i ref={close_but} onClick={()=> closeBio()} className="fas close_mobile_bio fa-times"></i></Link>
                <Img src={item.img} />
                <div className="mobile_bio_main">
                <Header name={item.name}/>
                {item.id != currentUser.id ? <Button item={item}  closeBio={closeBio}/> : ''}
                <BioContacts args={item}/>
                <ExBut isId={item.id == currentUser.id}/>
                </div>
            </div>
        </div>
        
    )
}

export default MobileBio