import { useContext } from "react"
import Context from "../../Context/Context"
import getUsers from "../../hooks/getUsers"
import { Link } from "react-router-dom"
import { useDispatch } from "react-redux"
import './header.css';



function TreadHeader(){
    let dispatch = useDispatch()
    const {currentPerson, usersDate, currentUser} = useContext(Context)

    async function upload(){
        await getUsers(disp)
        await setTimeout(changeUsers, 100)
    }

    function disp(arg){
        dispatch({type: 'SET_UD', item: arg})

    }

    async function changeUsers(){
        await usersDate.forEach((item=>{
            if( item.id == currentPerson.id) dispatch({type:'SET_C_P', item: item})
            if( item.id == currentUser.id) dispatch({type:'SET_C_U', item: item})

        }))
    }


    function BackBut(){
        dispatch({type: 'REMOVE_TR'})
        dispatch({type:'REMOVE_C_P'})
    }

  
    return(
        <div className="news_header">
        <Link to='/general' style={{fontSize: '1em'}}  onClick={()=> BackBut()} className="return_news_main"><i className="fas some_little fa-arrow-left"></i></Link>
        <div >{currentPerson.name}</div>
        <button style={{fontSize: '1em'}} onClick={()=> upload()} className="upload_messages"><i className="fas fa-sync-alt"></i></button>
        </div>

    )
}
export default TreadHeader
