import { useContext, useEffect, useRef, useState } from "react";
import Context from "../../Context/Context";
import { useDispatch } from "react-redux";
import './news.css';
import { Link } from "react-router-dom";
import finalPropsSelectorFactory from "react-redux/es/connect/selectorFactory";
import setPosts from "../../hooks/setPosts";

function Post(prop){
    let dispatch = useDispatch()
    const {usersDate, currentUser} = useContext(Context)
    let [choosed, setChoosed] = useState(false)
    let block = useRef()


  
useEffect(()=>{
    commitBlock()
}, [])



function logLength(){
    if(typeof (prop.item) != 'string') {
        let id = `${Math.floor(Math.random() * 100000)}` + `${prop.item.postContent.length}`
        return id
    }
    
}
logLength()

////////////////////////////
function commitBlock(){
    if(prop.dp.includes(prop.item.postId)){
        if(block.current.classList.value.includes('choosed_bl')){
            return true
        }
        setChoosed(true)
        block.current.classList.add('choosed_bl')
    }
  }

  function setBlocks(){
    if(!choosed){
        if(prop.item.id == prop.cu.id){
            prop.sdp([...prop.dp, prop.item.postId])
        }
    }
    else{
        block.current.classList.remove('choosed_bl')
        setChoosed(false)
        let newArray = prop.dp.filter(elem=>{ 
            if(elem != prop.item.postId) return true
        })
        prop.sdp(newArray)
    }
    
    
  }


  async function deleteBlock(){
    let newPosts
    if(choosed){
        newPosts = await prop.pd.map(item=>{
            return item.filter(elem=>{if(elem != prop.item){return true}})
        })
        newPosts = newPosts.filter(item=> {if(item.length>1) {return true}} )
        dispatch({type: 'SET_P', item: await newPosts})
        setPosts(newPosts)
        console.log(await newPosts);
    }

}


///////////////////////////////////

    function openProfile (){
        let user;
        usersDate.forEach(el=>{ if( el.id === prop.item.id) user = el })
        dispatch({type: 'SET_C_P', item: user})
    }

/////////////////////////////////


    function createImg(prop){
        if(window.screen.width > 500){
            return(<img src={require(`../../${prop.item.imgUrl}`)} id={prop.item.id} onClick={()=> openProfile()} className="post_img" alt=""/>)
        }
        else{
            return(<Link to={'/bio'}><img src={require(`../../${prop.item.imgUrl}`)} id={prop.item.id} onClick={()=> openProfile()} className="post_img" alt=""/></Link>)
        }
    }


    ///////////////////////////

   
    //////////////////////////////


    if(typeof (prop.item) != 'string'){
        return(
            <div id={logLength()} onClick={()=> setBlocks()} className="post" >
            <div ref={block} className="post_body ">
                <div className="main_block">
                    <div className="person_img">
                      {createImg(prop)}
                    </div>
                    <div className="person_post">
                        <div onClick={(e)=> e.preventDefault()} className="person_name">{prop.item.postName}<div className="person_post_date">{prop.item.postDate}</div>
                        </div>
                        <div className="person_content">
                            {prop.item.postContent}
                        </div>
                    </div>
                    </div>
                    {choosed? <i onClick={()=> deleteBlock()} className="fas close_block fa-times"></i>: ''}
                    
                </div>
                
                
                </div>
        )
    }
    else{
        return(
            <div className="post_time">
                {prop.item}
            </div>
        )
            
    }
    
}
export default Post

