import { useContext } from "react"
import Context from "../../Context/Context"
import { useDispatch } from "react-redux";
import './nomad.css';
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";


function Nomad(){
    let navigate = useNavigate()
    let dispatch = useDispatch()
    const {currentUser, screenWidth} = useContext(Context)
    if(!currentUser.email){
        navigate('/')
    }
    
    function setter(){
            dispatch({type:'REMOVE_CT'})
            dispatch({type: 'REMOVE_C_P'}) 
    }

    return(
        <div className="nomad_list ">
            <div className="back_menu" onClick={()=> dispatch({type:'REMOVE_CT'})}>+</div>
            
            {screenWidth <= 500? <Link to={'/bio'}><button className="enter_but" onClick={(e)=> setter()}>Profile</button></Link>: ''}
        </div>
    )
}
export default Nomad
