let defaultState = {
    currentUser: JSON.parse(localStorage.getItem('currentUser')) || {},
    currentPerson: JSON.parse(localStorage.getItem('currentPerson')) || {},
    postsDate: JSON.parse(localStorage.getItem('postsDate')) || [],
    usersDate: JSON.parse(localStorage.getItem('usersDate')) || [],
    isPosts: JSON.parse(localStorage.getItem('isPoster')) || false,
    isTrade: JSON.parse(localStorage.getItem('isTrade')) || false,
    isContacts: JSON.parse(localStorage.getItem('isContacts')) || false,
    currentItem: {loging: '', hiden: ''},
    isReg: JSON.parse(localStorage.getItem('isReg')) || false,
    register: JSON.parse(localStorage.getItem('register')) || false,
    connection: true,
    customer: false
  }

const SET_C_U = 'SET_C_U'
const REMOVE_C_U = 'REMOVE_C_U'

const SET_C_P = 'SET_C_P'
const REMOVE_C_P = 'REMOVE_C_P'

const SET_P = 'SET_P'
const REMOVE_P = 'REMOVE_P'

const SET_UD = 'SET_UD'
const REMOVE_UD = 'REMOVE_UD'

const SET_I_P = 'SET_I_P'
const REMOVE_I_P = 'REMOVE_I_P'

const SET_TR = 'SET_TR'
const REMOVE_TR = 'REMOVE_TR'

const SET_CT = 'SET_CT'
const REMOVE_CT = 'REMOVE_CT'

const SET_CI = 'SET_CI'
const REMOVE_CI = 'REMOVE_CI'

const SET_IR = 'SET_IR'
const REMOVE_IR = 'REMOVE_IR'

const SET_RG = 'SET_RG'
const REMOVE_RG = 'REMOVE_RG'

const SET_CTR = 'SET_CTR'
const REMOVE_CTR = 'REMOVE_CTR'

const SET_CN = 'SET_CN'
export const currentUserReducer = (state = defaultState, action) => {

    switch(action.type) {
      case SET_C_U: return setCurrentUser(state, action.item)
      case REMOVE_C_U: return setCurrentUser(state, {})

      case SET_C_P: return setCurrentPerson(state, action.item)
      case REMOVE_C_P: return setCurrentPerson(state, {})

      case SET_P: return setPostsDate(state, action.item)
      case REMOVE_P: return setPostsDate(state, [])

      case SET_UD: return setUsersDate(state, action.item)
      case REMOVE_UD: return setUsersDate(state, [])

      case SET_I_P: return setIsPosts(state, true)
      case REMOVE_I_P: return setIsPosts(state, false)


      case SET_TR: return setIsTrade(state, true)
      case REMOVE_TR: return setIsTrade(state, false)

      case SET_CT: return setIsContacts(state, true)
      case REMOVE_CT: return setIsContacts(state, false)

      case SET_CI: return setCurrentItem(state, {loging: action.log, hiden: action.hide})
      case REMOVE_CI: return setCurrentItem(state, {loging: '', hiden: ''})

      case SET_IR: return setIsReg(state, true)
      case REMOVE_IR: return setIsReg(state, false)

      case SET_CTR: return setIsCustomer(state, true)
      case REMOVE_CTR: return setIsCustomer(state, false)

      case SET_RG: return setRegister(state, action.item)

      case SET_CN: return setConnection(state, action.item)

      default : return state

  }
}


function setCurrentUser(state, action){
  localStorage.setItem('currentUser', JSON.stringify(action))
return {...state, currentUser: action}
}

function setCurrentPerson(state, action){
  localStorage.setItem('currentPerson', JSON.stringify(action))
  return {...state, currentPerson: action}
}

function setPostsDate(state, action){
  localStorage.setItem('postsDate', JSON.stringify(action))
  return {...state, postsDate: action}
}

function setUsersDate(state, action){
  localStorage.setItem('usersDate', JSON.stringify(action))
  return {...state, usersDate: action}
}

function setIsPosts(state, action){
  localStorage.setItem('isPoster', JSON.stringify(action))
  return {...state, isPosts: action}
}

function setMobileBio(state, action){
  return {...state, mobileBio: action}
}

function setIsTrade(state, action){
  localStorage.setItem('isTrade', JSON.stringify(action))
  return {...state, isTrade: action}
}

function setIsContacts(state, action){
  localStorage.setItem('isContacts', JSON.stringify(action))
  return {...state, isContacts: action}
}

function setCurrentItem(state, action){
  return {...state, currentItem: action}
}

function setIsReg(state, action){
  localStorage.setItem('isReg', JSON.stringify(action))
  return {...state, isReg: action}
}

function setIsCustomer(state, action){
  localStorage.setItem('isCustomer', JSON.stringify(action))
  return {...state, customer: action}
}

function setRegister(state, action){
  localStorage.setItem('register', JSON.stringify(action))
  return {...state, register: action}
}

function setConnection(state, action){
  return {...state, connection: action}
}


