
import BioContacts from "../Contacts/BioContacts"
import Button from "../Button/Button"
import Header from "../Header/Header"
import Img from "../Img/Img"
import React, { useContext} from "react"
import Context from "../../Context/Context"
import ExBut from "../ExBut/ExBut"
import './bio.css'

function Bio() {
    const {currentUser, currentPerson} = useContext(Context)
   

    let item
    currentPerson.id ? item = currentPerson: item = currentUser
    return(
        <div className="bio">
            <div className="container">
                <Img src={item.img}/>
                <div className="bio_main">
                <Header name={item.name}/>
                {item.id != currentUser.id ? <Button item={item}/> : ''}
                <BioContacts args={item}/>
                </div>
                <ExBut isId={item.id == currentUser.id}/>
                
            </div>
        </div>
    )
}

export default Bio