import { useContext, useRef} from "react"
import Context from "../Context/Context"
import Header from "./Header/Header"
import News from "./News/News"
import Poster from "./Poster/Poster"
import TreadHeader from "./Header/TreadHeader"
import Treads from "./Treads/Treads"
import './main.css'


function Main(){
    const {currentUser, isTrade} = useContext(Context)
    let news = useRef()

    return(
        <div ref={news} className="news">
            <div className="container_news">
                {!isTrade ? (<><Header/><News news={news}/><Poster/></>) : (<><TreadHeader/><Treads tread={news}/><Poster/></>)}
            </div>
        </div>
    )
}
export default Main
