import './friends.css'
import {useDispatch} from 'react-redux';
import { Link } from 'react-router-dom';

function Friend(item, func){
    let dispatch = useDispatch()

    function createImg(item){
        if(window.screen.width > 500){
            return(<img src={require(`../../${item.item.img}`)} onClick={()=> dispatch({type:'SET_C_P', item: item.item})} id={item.item.id} className="friend_img" alt=""/>)
        }
        else{
            return(<Link to={'/bio'}><img src={require(`../../${item.item.img}`)} onClick={()=> dispatch({type:'SET_C_P', item: item.item})} id={item.item.id} className="friend_img" alt=""/></Link>)
        }
    }

    return(
        <div className="friend_item">
                {createImg(item)}
                <p className="friend_name c_hover">{item.item.name}</p>
            </div>
    )
}
export default Friend