import React from 'react'
import { useEffect } from 'react'
import scrolling from '../../hooks/scrolling'
import Tread from './Tread'
import { useContext } from "react"
import Context from "../../Context/Context"
import './treads.css'

function Treads(props) {
    const {currentUser, currentPerson, isTrade} = useContext(Context)
    useEffect(()=>{
      console.log(props);
      scrolling(props.tread, window.screen.width)
    
      }, [])

      
    if(currentUser.treads[currentPerson.id]){
  return (
    <div className="tread_main">
      {currentUser.treads[currentPerson.id].map(user=>{
        return (
            <Tread key={Math.random()} user={user}/>
        )
      })}
      
    </div>
  )
  }
}

export default Treads

