import { useRef } from 'react'
import './connection.css'
import { useSelector } from 'react-redux/es/exports';
import { useDispatch } from 'react-redux/es/exports';
export function ConnectionError(){
    let dispatch = useDispatch()
    let connection = useSelector(state => state.store.connection)
    let block = useRef()
    if(!connection){
        block?.current?.classList.add('animation');
        dispatch({type:'SET_CN', item: true})
    } 
    return(
        <div ref={block} className="connection_block">
            <div className="message">Bad connection or server works wrong</div>
        </div>
    )
}