import React from 'react'
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import './ex_but.css'

function ExBut (props){
    const dispatch = useDispatch()
    let navigate = useNavigate()
    let exiteCl = `exite_but_user`;
    let exiteText = `Close`
    if (props.isId) {
        exiteCl = `exite_but`;
        exiteText = `Exite`;
    }
    
    function Exite(e){
        if(e.target.textContent == `Exite`){
            dispatch({type: 'REMOVE_C_U'})
            dispatch({type: 'REMOVE_TR'})
            dispatch({type: 'REMOVE_IR'})
            navigate('/') 
        }
        else{
            dispatch({type: 'REMOVE_C_P'})
            dispatch({type: 'SET_I_P'})
            navigate('/general')
        }
    }

  return (
    <>
    <div className={exiteCl} onClick={ e => Exite(e)}>{exiteText}</div>
    </>
  )
}
export default ExBut