import { useContext } from "react"
import Context from "../../Context/Context"
import { useDispatch } from "react-redux"
import './header.css';


function Header(){
    let dispatch = useDispatch()
    const {setIsContacts} = useContext(Context)
    return(
        <div className="news_header">
        <div className="menu_open" onClick={()=> dispatch({type:'SET_CT'})}>
        <i className="fa-solid fa-bars bar" onClick={()=> dispatch({type:'SET_CT'})}></i>
        </div>
            Posts
        </div>
    )
}
export default Header