import { useContext, useEffect, useRef } from "react"
import Context from "../../../Context/Context"
import React from "react"
import getPosts from "../../../hooks/getPosts"
import move from "../../../hooks/move"
import { Link } from "react-router-dom"
import {useDispatch} from 'react-redux';
import './login.css'
import { useNavigate } from "react-router-dom"

function MobileLogin(prop){
    const dispatch = useDispatch()
    let navigate = useNavigate()
    let email = useRef()
    let pass = useRef()
    let closer = useRef()
    const hideBlock = useRef()
    let iteme = useRef()
    let timer1, timer2

    let confE = []
    let confP = []    
    const {usersDate, register} = useContext(Context)
    useEffect(()=>{
        move(register, iteme, hideBlock)
        dispatch({type: 'SET_CI', log: hideBlock, hide: iteme}) 
    }, [iteme])



    async function Reg(){
        usersDate.forEach(item=>{
            if(email.current.value == item.email && pass.current.value == item.password){
                    dispatch({type: 'SET_C_U', item: item})
                    move(true, iteme, hideBlock)
                    dispatch({type:'SET_IR'})
                    getPosts(posts)
                    navigate('/general')
                    return false
            }
            if(email.current.value != item.email) confE.push(false)
            if(pass.current.value != item.password) confP.push(false)
            

        })
        callMistake(confE, confP)
        confE = []
        confP = []
    }

    function callMistake(arr1, arr2){
        console.log(arr1.length);
        console.log(usersDate.length);
        if(arr1.length == usersDate.length){
            missedE()
            missedP()
        } 
        if(arr2.length == usersDate.length) missedP()
    }

    function missedE(){
        console.log('aaaaaaaaaaaaaaaaaaaaaaaaaaaaaas');
        email.current.style.borderBottom = '3px solid red'
        email.current.value = ''
        timer1 = setTimeout(()=>{
            if(email.current) email.current.style.borderBottom = '0.5px solid rgba(0, 0, 0, 0.24)'
            clearTimeout(timer1)
        }, 2000)
        
    }
    function missedP(){
        console.log('bbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbb');
        pass.current.style.borderBottom = '3px solid red'
        pass.current.value = ''
        timer2 = setTimeout(()=>{
            if(pass.current) pass.current.style.borderBottom = '0.5px solid rgba(0, 0, 0, 0.24)'
            clearTimeout(timer2)
        }, 2000)
        
    }

    function posts(arg){
        dispatch({type: 'SET_P', item: arg})
        dispatch({type: 'SET_I_P'})
    }
    return(
<>
            <div ref={hideBlock} className="hide_window" style={{display: 'flex'}}></div>
        <div ref={iteme} className="login_window">
        <div className="container">
                          
            <h2 className="login_lable">Log in</h2>
            <input ref={email} type="text" className="login_in bio_inputs" placeholder="Login"/>
            <input ref={pass} type="text" className="password_in" placeholder="Password"/>
            <div className="reg_active" onClick={()=> prop.setRegister(true)}>Create account</div>
            <div to='/bio' className="log_but" onClick={()=> Reg()}>Enter</div>
        </div>
        </div>
        </>
    )
}
export default MobileLogin