function scrolling(elem, width){
  if(width <= 500){
    window.scrollTo({
      top: elem.current.clientHeight- (window.innerHeight + 500)
    })
  setTimeout(() => {
    window.scrollBy({
      top: 500,
      behavior: "smooth"
      })
  }, 20);
  
  }
  
  else{
    window.scrollTo({
        top: elem.current.clientHeight- (window.innerHeight + 500)
      })
    
    window.scrollBy({
        top: 600,
        behavior: "smooth"
        })
      }
}

export default scrolling